import {Injectable, OnInit} from '@angular/core';
import {Router} from "@angular/router";
declare const firebase: any;
declare const $:any;
@Injectable()
export class DataService {
  public navshow = false;
  public data:any;
  public loginerror : any;
  public userdet: any;
public userkey :any
constructor(private router:Router) {
firebase.database().ref('details').on('value', (snapshot) => {
  let courses = []
  const det = Object.keys(snapshot.val()).map(k => {return snapshot.val()[k]})
  det.forEach((e) => {
    e.publications.forEach(d => {
      d['author'] = e;
      courses.push(d);
    });
  });
  this.data = courses;
});
}
login(user: any) {
  firebase.auth().signInWithEmailAndPassword(user.email, user.password).then((data: any) =>     {

     console.log('logged in');
    this.getdata();
    }
  )
    .catch((error: any) => {
      const errorCode = error.code;
      const errorMessage = error.message;
      if (errorCode === 'auth/wrong-password') {
        console.log('wrong password');
      } else {
        console.log('wrong password');
      }
      this.loginerror = 'Sorry, ' + error.code;
      $('#loginbox').css({
        'border' : '1px solid red'
      })
      setTimeout(() => {
        this.loginerror = '';
      },5000);

    });
}
getdata()
{
  const user  = firebase.auth().currentUser
  if(user) {
    firebase.database().ref().child("details").orderByChild("email").equalTo(user.email).once('value', (snap) => {
      this.userdet = snap.val();
      this.userkey = Object.keys(this.userdet).map(k => {return k; })[0]
      this.userdet = Object.keys(this.userdet).map(k => {
        return this.userdet[k];
      })[0]
    });
  }
}
signup(s: any) {
  firebase.auth().createUserWithEmailAndPassword(s.email, s.password).catch(function(error) {
    // Handle Errors here.
    this.loginerror = 'Sorry, ' + error.code;    // ...
    setTimeout(() => {
      this.loginerror = '';
    },5000);
    $('#signupbox').css({
      'border' : '1px solid red'
    })
  }).then(() => {
    const nee = {
      name : s.name,
      phone : '',
      links : '',
      email : s.email,
      company : '',
      about : '',
      publications : [],
      qualifications : [] }
    firebase.database().ref('details').push(nee).then(() => {
      this.loginerror = 'Successfully signed up ';
      setTimeout(() => {
        this.loginerror = '';
      }, 5000);
    });


  });
}
  isAuthenticated() {
    let user = firebase.auth().currentUser;

    if(user) {
if(!this.userdet)
{
  this.getdata()
}
      return true;
    } else {

      return false;
    }
  }
  logout() {


console.log("asdf")
    firebase.auth().signOut().then(() => {

      this.router.navigate(['../login']);
    }, (error: any) =>{
      console.log(error);
    });

  }
  update()
  {

    firebase.database().ref("details/" + this.userkey).remove();
    firebase.database().ref("details").push(this.userdet);
  }
}



