import {Component, OnInit, OnDestroy} from '@angular/core';
import {ActivatedRoute, Params, Router} from '@angular/router';
import {Subscription} from 'rxjs';
import {DataService} from "../../services/data.service";

@Component({
  selector: 'app-course',
  templateUrl: './course.component.html',
  styleUrls: ['./course.component.css']
})
export class CourseComponent implements OnInit, OnDestroy {

  public data: any;
  public sub: any;
  private route$: Subscription;
  constructor(private route: ActivatedRoute, private datas: DataService,private rou: Router)  {
    this.datas.navshow = true;   }
  ngOnInit() {
    this.sub = this.route
      .queryParams
      .subscribe(params => {
        // Defaults to 0 if no query param provided.

        this.data = JSON.parse(params['id']);

        console.log(params['id']);
      });
  }
  ngOnDestroy() {
    this.sub.unsubscribe();
  }
  author()  {
    let cache = [];
    const t = JSON.stringify(this.data, function(key, value) {
      if (typeof value === 'object' && value !== null) {
        if (cache.indexOf(value) !== -1) {
          // Circular reference found, discard key
          return;
        }
        // Store value in our collection
        cache.push(value);
      }
      return value;
    });
    cache = null;
    console.log(t)
    this.rou.navigate(['../author'], {queryParams : {id: t}});
  }
}



