import {AfterViewInit, Component} from '@angular/core';
import {DataService} from '../../services/data.service';
import {Router} from "@angular/router";
declare const $: any;
@Component({
  selector: 'app-spread',
  templateUrl: './spread.component.html',
  styleUrls: ['./spread.component.css']
})
export class SpreadComponent implements AfterViewInit {
  public title = 'None';
  public type = 'None';
  public changed = true;
  public until = 0;
  public datas: any;
constructor(private data: DataService,private route: Router) {
  this.data.navshow = true;
  this.datas = this.data.data;
}
ngAfterViewInit() {
  $('.ui.radio.checkbox')
    .checkbox();

  $('.selection.dropdown')
    .dropdown();
}

category(s = this.title ,d = this.type) {
  this.title = s;
  this.type = d;
  if ( s === 'None' && d === 'None')  {
  this.datas = this.data.data;
  } else if (s !== 'None' && d === 'None') {
    let re = []
    this.data.data.forEach( r => {
      if (r.course === s ) {
       re.push(r);      }
    });
    this.datas = re;
      } else if (s === 'None' && d !== 'None') {
    let re = []
    this.data.data.forEach( r => {
      if (r.type === d ) {
        re.push(r);      }
    });
    this.datas = re;
  }
  else {
    let re = []
    this.data.data.forEach( r => {
      if (r.course === s && r.type === d) {
        re.push(r);      }
    });
    this.datas = re;
  }

}
check() {
  if (this.data.data && this.until == 0) {
    this.datas = this.data.data;

    console.log(this.datas)
    this.until = 1;  }
  return true;
}
move(s: any) {
let cache = [];

  const t = JSON.stringify(s, function(key, value) {
    if (typeof value === 'object' && value !== null) {
      console.log(value)
      if (cache.indexOf(value) !== -1) {
        // Circular reference found, discard key
        return;
      }
      // Store value in our collection
      cache.push(value);
    }
    return value;
  });
  cache = null;

  this.route.navigate(['../course'], {queryParams: {id: t}});
}
}




